.name a {
    text-decoration: none;
    font-family: Inter, serif;
    font-style: normal;
    font-weight: 500;
    font-size: 24px;
    color: #0064EB;
}

.description{
    margin-bottom: 24px;
    font-family: Inter, serif;
    font-size: 16px;
    color: #000000;
    padding: 0 32px;
}

.name {
    margin-top: 24px;
    padding: 0 32px;
}

.repoWrapper{
    background-color: white;
    border: solid white 1px;
    border-radius: 6px;
    margin: 24px 56px;
    flex-direction: column;
    display: flex;
}

@media only all and (max-width: 640px){
    .repoWrapper{
        margin: 18px 20px;
    }
    .description{
        padding: 0 10px;
    }

    .name {
        padding: 0 10px;
    }
}